.l-timeline__items {
  position: relative;
  padding: 1px 0; }

.l-timeline__items:after {
  content: "";
  position: absolute;
  top: 0;
  left: 10px;
  height: 100%;
  width: 2px;
  margin-left: -1px;
  background: #7aa104; }

@media (min-width: 992px) {
  .l-timeline__items:after {
    left: 50%; } }

.b-timeline-descr {
  color: #676d72;
  margin-bottom: 30px;
  max-width: 560px;
  font-size: 0.85714em; }

.b-timeline-item {
  position: relative;
  margin-bottom: 60px;
  padding-left: 60px;
  font-size: 14px; }
  .b-timeline-item:last-child {
    margin-bottom: 0; }
  .b-timeline-item__date {
    display: inline-block;
    padding: 5.5px 16px;
    margin-top: 20px;
    background-color: #b01e21;
    color: #fff;
    font-weight: 600; }
  .b-timeline-item__title {
    font-weight: 700;
    font-size: 18px;
    margin: 20px 0; }
  .b-timeline-item__text {
    color: #676d72;
    font-style: italic; }
  .b-timeline-item__title:after {
    content: "";
    position: absolute;
    width: 25px;
    height: 25px;
    background: #fff;
    left: -3.5px;
    border-radius: 50%;
    border: 2px solid #7aa104;
    z-index: 2; }
  @media (min-width: 992px) {
    .b-timeline-item {
      height: 250px;
      padding-left: 0;
      overflow: hidden; }
      .b-timeline-item__picture, .b-timeline-item__content {
        max-width: 50%;
        padding: 0 35px; }
      .b-timeline-item__content {
        position: absolute;
        top: 0;
        right: 0;
        padding-top: 20px;
        padding-bottom: 20px; }
      .b-timeline-item__title:after {
        left: 0;
        margin-left: -12.5px; }
      .b-timeline-item--inverted .b-timeline-item__picture {
        position: absolute;
        top: 0;
        right: 0; }
      .b-timeline-item--inverted .b-timeline-item__content {
        position: static;
        text-align: right; }
      .b-timeline-item--inverted .b-timeline-item__title:after {
        left: 50%; } }
